import React, {Ref, RefObject, useCallback, useRef} from 'react';
import {
  ImageBackground,
  Platform,
  StyleSheet,
  useWindowDimensions,
} from 'react-native';
import {IconButton, List, ProgressBar} from 'react-native-paper';
import {useQuery} from 'react-query';
import {
  CBProgressParams,
  genExecTime,
  getActiveProjectLayer,
  getProjectMedia,
} from '../../api/media';
import {useAppDispatch, useAppSelector} from '../../store/hooks';
import {useIsFocused} from '@react-navigation/native';
import {
  setEncoderProgress,
  setCaretPosition,
  setScrollPosition,
  setIsPlaying,
  setActiveLayer,
} from '../../store/slices/editor';
import {ResizeMode, Video} from 'expo-av';
import {EditorLayer, EditorProject, Media} from '../../types';

const styles = StyleSheet.create({
  video: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
  },
  chip: {
    fontSize: 18,
  },
});
function EditorVideoComponent({
  video2,
  mediaRes,
  layers,
  projectId,
}: {
  projectId: string;
  layers: EditorLayer[];
  mediaRes: EditorProject;
  video2: RefObject<JSX.IntrinsicElements['video'] | undefined>;
}) {
  const dispatch = useAppDispatch();
  const encoderProgress = useAppSelector(
    state => state.editor.encoderProgress,
  );
  const pixelsPerSecond = useAppSelector(
    state => state.editor.pixelsPerSecond,
  );
  const caretPosition = useAppSelector(
    state => state.editor.videoCaretPosition,
  );
  const scrollPosition = useAppSelector(
    state => state.editor.videoScrollPosition,
  );
  const video3 = useRef<Video | null>();
  const isPlaying = useAppSelector(state => state.editor.isPlaying);
  const canScroll = useAppSelector(state => state.editor.canScroll);
  const scroll = useAppSelector(state => state.editor.scroll);
  const activeLayer = useAppSelector(
    state => state.editor.activeLayer,
  );
  const focused = useIsFocused();
  const currentPosition =
    (caretPosition + scrollPosition) / pixelsPerSecond || 0.0;
  const encodedMediaQuery = useQuery(
    ['encodedMedia', projectId, currentPosition, activeLayer],
    async () =>
      await getProjectMedia(
        layers!,
        currentPosition,
        (progress: CBProgressParams) =>
          dispatch(setEncoderProgress(progress.progress)),
      ),
    {
      enabled:
        mediaRes?.id !== undefined && layers?.length && focused
          ? true
          : false,
      keepPreviousData: false,
      onSuccess: () => {
        console.info('after query');
        if (video2?.current)
          video2.current.currentPosition =
            encodedMediaQuery.data?.currentPosition || 0.0;
      },
    },
  );

  const queryPosition =
    encodedMediaQuery.data?.currentPosition || 0.0;
  const offsetPosition = encodedMediaQuery.data?.offset || 0.0;
  // console.info('queryPosition', queryPosition)
  // console.info('offsetPosition', offsetPosition)
  const onTimeUpdate = useCallback(
    (currentTime: number, paused: boolean) => {
      try {
        const newtime =
          (offsetPosition || 0) +
          currentTime -
          (encodedMediaQuery.data?.activeMedia.encoding.start || 0);

        if (newtime >= encodedMediaQuery.data?.totalTime) {
          if (Platform.OS === 'web') video2.current?.pause();
          else video3.current?.setStatusAsync({shouldPlay: false});
          dispatch(setIsPlaying(false));
        }

        const newoffset = newtime * pixelsPerSecond;
        const activeLayerId = getActiveProjectLayer(layers!, newtime)
          ?.activeMedia?.id;
        const layerSwitched =
          activeLayerId && activeLayerId !== activeLayer;
        let nscrollPosition = 0;
        let ncaretPosition = caretPosition;

        if (!paused) {
          if (canScroll) {
            const offset = newoffset - scroll;
            console.info('offset', offset, scroll, newoffset);
            if (offset > 0) {
              const diff = newoffset - offset;
              console.info('diff', diff, offset, scroll, newoffset);
              nscrollPosition = diff;
              ncaretPosition = offset;
            } else {
              nscrollPosition = newoffset - caretPosition;
            }
          } else {
            ncaretPosition = newoffset;
          }
          dispatch(setIsPlaying(true));
          dispatch(setCaretPosition(ncaretPosition));
          dispatch(setScrollPosition(nscrollPosition));
        }
        if (paused && isPlaying && Platform.OS === 'web')
          dispatch(setIsPlaying(false));
        if (layerSwitched) {
          // console.info('layerSwitched', layerSwitched, activeLayer, activeLayerId)
          dispatch(
            setActiveLayer({
              activeLayerId,
              scrollPosition: nscrollPosition,
              caretPosition: ncaretPosition,
            }),
          );
        }
      } catch (err) {
        console.error('ontimeupdate rr', err);
      }
    },
    [encodedMediaQuery, canScroll, isPlaying, layers, offsetPosition],
  );
  const {width, height} = useWindowDimensions();
  if (
    (encoderProgress && encoderProgress !== 1) ||
    encodedMediaQuery.isFetching
  )
    return (
      <ProgressBar
        style={{
          height: '100%',
          width: '100%',
          position: 'absolute',
          top: 0,
        }}
        progress={encoderProgress}
      />
    );
  const fileType = encodedMediaQuery.data?.activeMedia?.fileType;
  const isVideo = fileType?.search('video') > -1 || false;
  const isImage = fileType?.search('image') > -1 || false;
  if (isImage)
    return (
      <ImageBackground
        source={{uri: encodedMediaQuery.data.url}}
        style={{width: '100%', height: '100%'}}
      />
    );
  if (isVideo)
    return (
      <>
        {Platform.OS === 'web' ? (
          <video
            controls={false}
            style={styles.video}
            src={(encodedMediaQuery.data?.url as string) || ''}
            muted={encodedMediaQuery.data?.muted}
            ref={e => {
              // console.info('rerender video')
              // console.info("ref", e, encodedMediaQuery.data)
              if (e) {
                e.currentTime = queryPosition;
                video2.current = e;
                if (isPlaying) e.play();
              }
            }}
            onPlaying={e => {
              dispatch(setIsPlaying(true));
            }}
            onPause={e => {
              dispatch(setIsPlaying(false));
            }}
            onEnded={p => {
              console.info('ended', p);
            }}
            onTimeUpdate={e =>
              onTimeUpdate(
                (
                  e.nativeEvent
                    .target as JSX.IntrinsicElements['video']
                ).currentTime,
                (
                  e.nativeEvent
                    .target as JSX.IntrinsicElements['video']
                ).paused,
              )
            }
          />
        ) : (
          <Video
            source={{
              uri: (encodedMediaQuery.data?.url as string) || '',
            }}
            positionMillis={parseInt(queryPosition * 1000)}
            shouldPlay={isPlaying}
            ref={e => {
              console.info('onref', e);
              video3.current = e;
              if (isPlaying) e?.setStatusAsync({shouldPlay: true});
            }}
            isMuted={encodedMediaQuery.data?.muted}
            style={{
              width,
              height: height / 3,
              position: 'absolute',
              left: -15,
              top: 0,
              padding: 0,
              margin: 0,
            }}
            volume={1}
            resizeMode={ResizeMode.CONTAIN}
            useNativeControls={false}
            onPlaybackStatusUpdate={async status => {
              if (status.isLoaded && status.positionMillis) {
                onTimeUpdate(
                  status.positionMillis / 1000,
                  !status.isPlaying,
                );
              }
            }}
          />
        )}
      </>
    );
}
export default EditorVideoComponent;
